@media all {
    .aa-esistatus-header-addition {
        display: block;
    }

    .aa-esistatus .panel-footer .panel-heading {
        cursor: pointer;
    }

    .aa-esistatus .panel-footer .panel-default,
    .aa-esistatus .panel-footer .panel-default .panel-body p:last-child {
        margin-bottom: 0;
    }

    .aa-esistatus-footer {
        font-size: 85%;
    }
}
